import React, { useState, useEffect } from "react";
import { useLocation } from "react-router-dom";

const FormUrHelp = () => {
  const [formData, setFormData] = useState({
    name: "",
    phone: "",
  });

  const handleSubmit = async (e) => {
    e.preventDefault();

    try {
      const response = await fetch("http://localhost:8000/api/consultations/", {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify(formData),
      });

      if (!response.ok) throw new Error(" ");

      console.log("  ");
      setFormData({ name: "", phone: "" });
    } catch (error) {
      console.error(":", error);
    }
  };

  const formatPhoneNumber = (value) => {
    const numbers = value.replace(/\D/g, "");
    let trimmed = numbers.slice(0, 11);
    if (trimmed.length > 0 && !trimmed.startsWith("7")) {
      trimmed = "7" + trimmed.slice(1);
    }
    let formatted = "+7";
    if (trimmed.length > 1) {
      formatted += ` (${trimmed.slice(1, 4)}`;
    }
    if (trimmed.length >= 5) {
      formatted += `) ${trimmed.slice(4, 7)}`;
    }
    if (trimmed.length >= 8) {
      formatted += `-${trimmed.slice(7, 9)}`;
    }
    if (trimmed.length >= 10) {
      formatted += `${trimmed.slice(9, 11)}`;
    }

    return formatted;
  };

  const location = useLocation();

  useEffect(() => {
    if (location.hash === "#consultation-section") {
      const element = document.getElementById("consultation-section");
      element?.scrollIntoView({
        behavior: "smooth",
        block: "start",
      });
    }
  }, [location.hash]);

  const handleNameChange = (e) => {
    const inputValue = e.target.value;
    const cleanValue = inputValue.replace(/[^a-zA-Z--\s-]/g, "");
    setFormData((prev) => ({ ...prev, name: cleanValue }));
  };

  return (
    <section className="consultation-section" id="consultation-section">
      <div className="legal-grid-bg">
        {[...Array(16)].map((_, i) => (
          <div key={i} className="grid-item" />
        ))}
      </div>
      <div className="consultation-container">
        <div className="form-header">
          <h2>    </h2>
          <p className="form-subtitle">
              ,    
               
          </p>
          <div className="animated-divider">
            <div className="divider-part"></div>
            <div className="divider-part"></div>
            <div className="divider-part"></div>
          </div>
        </div>
        <form onSubmit={handleSubmit} className="consultation-form">
          <div className="form-step">
            <span className="step-number">1</span>
            <div className="form-group">
              <label> </label>
              <input
                type="text"
                required
                placeholder="  "
                value={formData.name}
                onChange={handleNameChange}
              />
            </div>
          </div>
          <div className="form-step">
            <span className="step-number">2</span>
            <div className="form-group">
              <label> </label>
              <input
                type="tel"
                required
                placeholder="+7 (999) 999-99-99"
                value={formData.phone}
                onChange={(e) => {
                  const formatted = formatPhoneNumber(e.target.value);
                  setFormData({ ...formData, phone: formatted });
                }}
              />
            </div>
          </div>
          <button type="submit" className="submit-btn">
             
          </button>
          <p className="form-notice">
             ,       
          </p>
        </form>
        <div className="company-info">
          <h3> ""</h3>
          <p> </p>
        </div>
      </div>
    </section>
  );
};

export default FormUrHelp;
