import React, { useState } from "react";

const Contact = () => {
  const [formData, setFormData] = useState({
    name: "",
    phone: "",
    email: "",
    service: "",
  });

  const handleSubmit = async (e) => {
    e.preventDefault();

    try {
      const response = await fetch(
        "http://localhost:8000/api/service-requests/",
        {
          method: "POST",
          headers: {
            "Content-Type": "application/json",
          },
          body: JSON.stringify(formData),
        }
      );

      if (!response.ok) throw new Error(" ");

      console.log("  ");
      setFormData({ name: "", phone: "", service: "" }); //  
    } catch (error) {
      console.error(":", error);
    }
  };

  const formatPhoneNumber = (value) => {
    const numbers = value.replace(/\D/g, "");
    let trimmed = numbers.slice(0, 11);
    if (trimmed.length > 0 && !trimmed.startsWith("7")) {
      trimmed = "7" + trimmed.slice(1);
    }
    let formatted = "+7";
    if (trimmed.length > 1) {
      formatted += ` (${trimmed.slice(1, 4)}`;
    }
    if (trimmed.length >= 5) {
      formatted += `) ${trimmed.slice(4, 7)}`;
    }
    if (trimmed.length >= 8) {
      formatted += `-${trimmed.slice(7, 9)}`;
    }
    if (trimmed.length >= 10) {
      formatted += `${trimmed.slice(9, 11)}`;
    }
    
    return formatted;
  };

  const handleNameChange = (e) => {
    const inputValue = e.target.value;
    const cleanValue = inputValue.replace(/[^a-zA-Z--\s-]/g, '');
    setFormData((prev) => ({ ...prev, name: cleanValue }));
  };

  return (
    <main className="contact">
      <div className="contacts">
        <div className="contacts__item">
          <div className="contacts__icon"></div>
          <h4 className="contacts__title"></h4>
          <p className="contacts__text">
            391710,  , . - , . . ,
            . , . , . 11
            <br />
            -: 09:00 - 20:00
          </p>
        </div>
        <div className="contacts__item">
          <div className="contacts__icon"></div>
          <h4 className="contacts__title"></h4>
          <p className="contacts__text">support@mail.ru</p>
        </div>
        <div className="contacts__item">
          <div className="contacts__icon"></div>
          <h4 className="contacts__title"></h4>
          <p className="contacts__text">+7-920-983-00-00</p>
        </div>
      </div>
      <div className="consultation">
        <h2 className="consultation__title">
                 ?
        </h2>
        <p className="consultation__text">
                  ,  
             .
        </p>
      </div>
      <div className="form-section">
        <form className="form" onSubmit={handleSubmit}>
          <div className="form-group">
          <input
              type="text"
              name="name"
              value={formData.name}
              onChange={handleNameChange}
              placeholder=" *"
              required
            />
            <input
              type="tel"
              name="phone"
              value={formData.phone}
              onChange={(e) => {
                const formatted = formatPhoneNumber(e.target.value);
                setFormData({ ...formData, phone: formatted });
              }}
              placeholder="  *"
              required
            />
          </div>
          <div className="form-group">
            <select
              required
              id="select__group"
              name="service"
              value={formData.service}
              onChange={(e) =>
                setFormData({ ...formData, service: e.target.value })
              }
            >
              <option value=""> *</option>
              <option value="consultation"></option>
                 SERVICE_CHOICES   Django
              <option value="family_law"> </option>
              <option value="housing_disputes"> </option>
              <option value="labor_disputes"> </option>
            </select>
          </div>
          <button id="contact__button" type="submit">
             
          </button>
        </form>
      </div>
      <div className="map">
        <iframe
          src="https://yandex.ru/map-widget/v1/?um=constructor%3Ac508a2a3ff9cf24ab0fd288b5437a0b9d7cc971f56501c853b157fc847ea0c83&amp;source=constructor"
          width="100%"
          height="400"
          title=" "
          frameborder="0"
        ></iframe>
      </div>
    </main>
  );
};

export default Contact;
